

.Button {
  border-width: 1px;
  --tw-border-opacity: 1;
  border-color: rgb(51 89 247 / var(--tw-border-opacity, 1));
  --tw-bg-opacity: 1;
  background-color: rgb(51 89 247 / var(--tw-bg-opacity, 1));
  padding-top: 0.25rem;
  padding-bottom: 0.25rem;
  padding-left: 1rem;
  padding-right: 1rem;
  --tw-text-opacity: 1;
  color: rgb(255 255 255 / var(--tw-text-opacity, 1));
  transition: 0.1s;
}
.Button:hover {
  --tw-border-opacity: 1;
  border-color: rgb(30 64 175 / var(--tw-border-opacity, 1));
  --tw-bg-opacity: 1;
  background-color: rgb(30 64 175 / var(--tw-bg-opacity, 1));
}
.Button:focus-visible {
  outline-color: black;
}
.Button.Button--secondary {
  --tw-text-opacity: 1;
  color: rgb(51 89 247 / var(--tw-text-opacity, 1));
  background: transparent;
}
.Button.Button--secondary:hover {
  --tw-bg-opacity: 1;
  background-color: rgb(191 219 254 / var(--tw-bg-opacity, 1));
}
.Button.Button--secondary:focus-visible {
  outline-color: var(--outline-current);
}
